<template>
    <div>

        <div class="info-agen">
              A <br>
            Address : , 4,  , Tel: 095-086-1353
        </div>
        <div class="deco">
            <table class="table-bordered" style="margin-left:100px;">
                <thead>
                    <tr style="text-align:center; color:#005086; background-color: #f4f6ff" class="table">
                        <th scope="col"></th>
                        <th scope="col"></th>
                        <th scope="col"></th>
                        <th scope="col"></th>
                        <th scope="col">/</th>
                        <th scope="col"></th>
                    </tr>
                </thead>
                <tbody style="font-size:18px; text-align:center; background-color:#fcf8f3" class="table">
                    <tr>
                        <th scope="row">1</th>
                        <td>/</td>
                        <td>{{can_food}}</td>
                        <td>400</td>
                        <th scope="col">
                            <input v-model="can_quan" placeholder="" type="number">
                            <button @click="increase_canFood(Number(can_quan))" class="btn btn-success"></button>
                            <button @click="increase_canFood(Number(can_quan))" class="btn btn-warning"></button>
                        </th>
                        <td>{{can_foodM}}</td>
                    </tr>
                    <tr>
                        <th scope="row">2</th>
                        <td></td>
                        <td>{{rice}}</td>
                        <td>200</td>
                        <th scope="col">
                            <input v-model="rice_quan" placeholder="">
                            <button @click="increase_rice(Number(rice_quan))" class="btn btn-success"></button>
                            <button @click="increase_canFood(Number(can_quan))" class="btn btn-warning"></button>
                        </th>
                        <td>{{riceM}}</td>
                    </tr>
                    <tr>
                        <th scope="row">3</th>
                        <td></td>
                        <td>{{water}}</td>
                        <td>1000</td>
                        <th scope="col">
                            <input v-model="water_quan" placeholder="">
                            <button @click="increase_water(Number(water_quan))" class="btn btn-success"></button>
                            <button @click="increase_canFood(Number(can_quan))" class="btn btn-warning"></button>
                        </th>
                        <td>{{waterM}}</td>
                    </tr>
                    <tr>
                        <th scope="row">4</th>
                        <td>/</td>
                        <td>{{shirt}}</td>
                        <td>300</td>
                        <th scope="col">
                            <input v-model="shirt_quan" placeholder="">
                            <button @click="increase_shirt(Number(shirt_quan))" class="btn btn-success"></button>
                            <button @click="increase_canFood(Number(can_quan))" class="btn btn-warning"></button>
                        </th>
                        <td>{{shirtM}}</td>
                    </tr>
                    <tr>
                        <th scope="row">5</th>
                        <td></td>
                        <td>{{pad}}</td>
                        <td>500</td>
                        <th scope="col">
                            <input v-model="pad_quan" placeholder="">
                            <button @click="increase_pad(Number(pad_quan))" class="btn btn-success"></button>
                            <button @click="increase_canFood(Number(can_quan))" class="btn btn-warning"></button>
                        </th>
                        <td>{{padM}}</td>
                    </tr>
                    <tr>
                        <th scope="row">6</th>
                        <td>/</td>
                        <td>{{teeth}}</td>
                        <td>100</td>
                        <th scope="col">
                            <input v-model="teeth_quan" placeholder="">
                            <button @click="increase_teeth(Number(teeth_quan))" class="btn btn-success"></button>
                            <button @click="increase_canFood(Number(can_quan))" class="btn btn-warning"></button>
                        </th>
                        <td>{{teethM}}</td>
                    </tr>
                    <tr>
                        <th scope="row">7</th>
                        <td></td>
                        <td>{{paracetamolM}}</td>
                        <td>100</td>
                        <th scope="col">
                            <input v-model="para_quan" placeholder="">
                            <button @click="increase_paracetamol(Number(para_quan))" class="btn btn-success"></button>
                            <button @click="increase_canFood(Number(can_quan))" class="btn btn-warning"></button>
                        </th>
                        <td>{{paracetamol}}</td>
                    </tr>
                </tbody>
        </table>
        </div>
    </div>
</template>

<script>
    export default {
        data(){
            return{
                can_food: 150,rice: 200,water: 300,
                bra: 100, shirt: 100,pad:50, teeth:200, firstAid:200, paracetamol:250,

                can_foodM: 5000,riceM: 2000,waterM: 3500,
                braM: 200, shirtM: 1000,padM:900, teethM:300, firstAidM:300, paracetamolM:500,
            }
        },
        methods:{
            increase_canFood(quantity){this.can_food+=quantity, this.can_foodM-=quantity},
            increase_rice(quantity){this.rice+=quantity, this.riceM-=quantity},
            increase_water(quantity){this.water+=quantity, this.waterM-=quantity},
            increase_bra(quantity){this.bra+=quantity, this.braM-=quantity},
            increase_shirt(quantity){this.shirt+=quantity, this.shirtM-=quantity},
            increase_pad(quantity){this.pad+=quantity, this.padM-=quantity},
            increase_teeth(quantity){this.teeth+=quantity, this.teethM-=quantity},
            increase_firstAid(quantity){this.firstAid+=quantity, this.firstAidM-=quantity},
            increase_paracetamol(quantity){this.paracetamol+=quantity, this.paracetamolM-=quantity},
      },
  }
</script>

<style>
.deco{
    margin: 20px 20px 20px 20px;
}
.info-agen{
    color: #111d5e;
    font-family: 'Trirong', serif;
    font-size: 20px;
    margin: 5px 0px 10px 20px;
    text-align: center;
}
.table{
    font-family: 'Trirong', serif;
    font-size: 20px; 
}
.bt-add{
    background-color: #cff6cf;
    color: black;
}
.bt-sub{
    background-color: #4d3e3e;
    color: whitesmoke;
}

</style>


