<template>
    <div>
        <nav class="navbar navbar-expand-lg navbar-light bg-light">
            <a class="navbar-brand" style="font-size:30px">
                DINOSAUR
                <img src="../img/handicrafts.png" alt="" style="width:50px;height:50px">
            </a>
            <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNavDropdown" aria-controls="navbarNavDropdown" aria-expanded="false" aria-label="Toggle navigation">
                <span class="navbar-toggler-icon"></span>
            </button>
            <div class="collapse navbar-collapse" id="navbarNavDropdown" style="font-size:20px">
                <ul class="navbar-nav">
                    <li class="nav-item">
                        <a class="nav-link"><router-link to="/" style="color:#776d8a">Home</router-link><span class="sr-only">(current)</span></a>
                    </li>
                    <li class="nav-item active">
                        <a class="nav-link"><router-link to="/UnitWarehouse" style="color:#776d8a">Donation</router-link></a>
                    </li>
                    <li class="nav-item">
                        <a class="nav-link"><router-link to="/Staff" style="color:#776d8a">Staff</router-link></a>
                    </li>
                </ul>
            </div>
        </nav>
        
        <div style="margin-top:20px; font-family: 'Trirong', serif; font-size:20px">
            <h1 style="text-align:center"></h1>
        </div>

        <div style="margin-left:585px;font-family: 'Trirong', serif;">
            <select v-model="selected" v-on:change="showInfo" class="selection-deco">
                <option disabled value=""></option>
                <option value="AgencyA"> A</option>
                <option value="AgencyB"> B</option>
                <option value="AgencyC"> C</option>
            </select>
        </div>

        <router-view></router-view> 
    </div>
</template>

<script>
export default {
    data(){
        return{
            today: new Date(),
            selected :'', 
        }
    },
    methods: {
        showInfo(){
            this.$router.push({path:"/"+this.selected})
        },

    },

}
</script>

<style>
  h2{
    text-align: center;
    margin: 15px auto 15px auto;
    color:whitesmoke
  }
  .topic-deco{
    border: tomato;
    border-width: 5px;
    border-style: solid;
    border-radius: 60px;
    margin: 15px 550px 15px 550px;
    background-color: tomato;
  }
  .selection-deco{
    width: 260px;
    height: 30px;
  }

</style>


